.Header {
  height: 50px;
  display: flex;
  justify-content: space-between;
  background: white;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 999;
}

.Header__logo {
  background-color: rgb(0, 154, 214);
  width: 200px;
  height: 50px;
}

.Header__user {
  height: 100%;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  vertical-align: middle;
  text-align: left;
  min-width: 220px;
  box-sizing: border-box;
  line-height: 50px;
  padding: 0 15px;
  cursor: pointer;
  background-color: #ffffff;
  position: relative;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.User__avatar {
  width: 30px;
  height: 30px;
  display: inline-block;
  vertical-align: middle;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  background-repeat: no-repeat;
  background-position: center center;
  border: 1px solid rgb(230, 228, 228);
  border-radius: 50%;
  background-size: contain;
  background-image: url("../../assets/icons/avatar.svg");
  text-align: center;
  font-size: 11px;
  line-height: 24px;
  box-sizing: border-box;
}

.User__name {
  position: relative;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 7px;
  font-size: 18px;
}
.User__name:hover {
  color: rgb(0, 154, 214);
}

.User__balance {
  position: absolute;
  top: 41px;
  right: 47px;
  font-size: 10px;
  line-height: 0;
}

.UserMenu {
  width: 100%;
  min-width: 220px;
  padding: 0 15px 10px 15px;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 50px;
  right: 0;
  background-color: rgb(230, 245, 251);
  -webkit-transform: translateY(0%);
  transform: translateY(0%);
  z-index: 20;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.UserMenu__actions {
  display: block;
  font-size: 16px;
  line-height: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
