.main {
  margin-top: 50px;
  width: 100%;
  min-height: 600px;
  margin-left: 200px;
  background-color: rgba(0, 0, 0, 0.03);
}

.EventName__wrapper {
  padding: 15px 20px 15px 15px;
  display: flex;
  align-items: flex-end;
  position: relative;
  background-color: #f6f8f8;
  border-bottom: 1px solid #dee5e7;
}

.EventName__input {
  flex-grow: 1;
  background-color: transparent;
  border: 0 !important;
  font-size: 24px;
  border-radius: 500px;
  padding: 6px 12px;
  outline: none;
}

.EventPage {
  margin-right: 0px !important;
  margin-left: 0px !important;
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
}
.column_left {
  flex-basis: 58.333%;
  max-width: 58.333%;
  box-sizing: border-box;
  flex: 0 0 auto;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
}
.EventConrols__wrapper {
  min-width: 400px;
  margin: 20px;
  margin-right: 0px;
  padding: 0;
  background-color: #ffffff;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.07);
  position: relative;
}
.EventConrols__content {
  font-size: 18px;
  padding: 15px 15px 0 15px;
}
.triggers {
}

#streetview {
}
#youtube {
}
#img {
}
#text {
}
#audio {
}
.EventModule {
  padding: 0 !important;
  transition: 0.3s;
  max-width: 100%;
  margin: 15px 0;
  overflow: hidden;
}
.m20 {
  margin-bottom: 2em;
}
.EventTimeLinitSetting {
  max-width: 100%;
  margin: 15px 0;
  padding: 0 0 40px 0;
  overflow: hidden;
  transition: 0.3s;
}

.component_event_block {
  max-width: 100%;
  margin: 15px 0 30px 0;
  padding: 0 0 10px 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.component_header {
  width: 100%;
  display: flex;
  align-items: baseline;
  position: relative;
  z-index: 1;
  background-color: #ffffff;
  margin-bottom: 20px;
}

.component_content {
  visibility: visible;
  margin-top: 0;
  height: auto;
  opacity: 1;
}

.component_name {
  font-size: 20px;
  padding: 0.25em 0 0 0;
  height: 30px;
  color: rgba(0, 0, 0, 0.7);
  flex-shrink: 1;
  transition: 0.5s;
  font-weight: normal;
}

.component_head {
  flex-shrink: 0;
  flex-grow: 1;
}

.EventHints__line {
  display: flex;
  margin-bottom: 10px;
}

.InputWithButtons__wrapper {
  width: 100%;
  padding: 0.8em 0.6em;
  position: relative;
  z-index: 1;
  font-size: inherit;
  display: inline-flex;
  flex-wrap: wrap;
  align-items: baseline;
}
.InputWithButtons__input_wrapper {
  flex-grow: 1;
}
.InputWithButtons__buttons {
  display: flex;
  align-items: center;
  margin: auto;
  position: absolute;
  right: 10px;
  bottom: 17px;
  background: transparent;
  border: 0;
  transition: 0.3s;
}
.InputWithButtons__input {
  width: 100%;
  border: 0 !important;
  border-radius: 0 !important;
  background: transparent;
  outline: 0;
  font-size: inherit;
  flex-grow: 1;
  padding-right: 30px;
}
.InputWithButtons__view {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 0;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1) !important;
  transition: 0.3s;
  z-index: -1;
}
.EventStreetView__error {
  margin-top: -15px;
  margin-bottom: 15px;
  display: block;
  font-size: 14px;
  line-height: 16px;
  color: rgb(231, 76, 60);
}
.EventStreetView__tutorial {
  margin: -10px 0 15px 0;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: -0.41px;
}
.link_blue {
  color: rgb(0, 154, 214);
  cursor: pointer;
  text-decoration: none;
}

.EventHintsLine {
  display: flex;
  margin-bottom: 10px;
}
.EventPicture__inner {
  background-color: rgba(1, 169, 224, 0.07);
  border-radius: 5px;
}
.EventPicture__dropzone {
  cursor: pointer;
}
.EventPicture__thumbs {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  position: relative;
  border-radius: 5px 5px 0px 0px;
  padding-left: 10px;
}
.EventPictureDropzone__info {
  width: 100%;
  height: 100px;
  font-size: 20px;
  color: rgb(0, 154, 214);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}
.PictureDropzone__dropzone_items {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 10px;
}
.PictureDropzone__dropzone_icon {
  content: "";
  display: inline-flex;
  width: 50px;
  height: 43px;
  background-image: url("../../assets/icons/picture.svg");
  background-repeat: no-repeat;
  transition: all 0.2s;
}
.PictureDropzone__dropzone_text {
  display: inline-flex;
  flex-direction: column;
  width: 75%;
}
.PictureDropzone__dropzone_subtext {
  font-size: 14px;
}

.PictureEditor__popup {
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 10000000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 31px 15px;
  background: #545454;
}
.PictureEditor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.PictureEditor__instructions {
  position: relative;
  width: 100%;
  margin: 0;
  padding-right: 60px;
  /* font-family: Helvetica, Neue; */
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #fff;
}
.PictureEditor__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  margin-top: 2px;
  background: url("");
  cursor: pointer;
}
.PictureEditor__picture {
  height: calc(100vh - 280px);
  padding: 26px 0;
  text-align: center;
}
.Loader__overlay {
  background-color: rgba(0, 0, 0, 0.5);
}
.Loader__row {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Loader__message {
  font-size: 18px;
}
.Loader__preloader {
  width: 35px;
  height: 35px;
  margin: 0 10px 0 0;
  display: inline-block;
  vertical-align: middle;
  position: relative;
}
.ReactCrop {
  position: relative;
  display: inline-block;
  cursor: crosshair;
  overflow: hidden;
  max-width: 100%;
  background-color: #000;
}
.rdw__editor_wrapper {
  box-sizing: content-box;
}
.rdw__edior_toolbar {
  visibility: visible;
  justify-content: flex-start;
  align-items: center;
  border: 0 !important;
  padding: 0;
  margin: 0;
}
.rdw__inline_wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 6px;
}
.rdw__option_wrapper {
  width: 30px;
  height: 30px;
  border-radius: 2px;
  border: 0;
  padding: 5px;
  min-width: 25px;
  margin: 0 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: white;
  text-transform: capitalize;
}
img {
  overflow-clip-margin: content-box;
  overflow: clip;
  box-sizing: border-box;
}
.rdw__link_wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 6px;
  position: relative;
}
.rdw__list_wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 6px;
}

.EventHTML__editor {
  min-height: 56px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1) !important;
  margin-top: 20px;
  margin-bottom: 5px;
  padding-bottom: 5px;
  padding-left: 2px;
  font-size: 16px;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
}
.draftEditorRoot {
  position: relative;
  height: inherit;
  text-align: initial;
}
.public_DraftEditorPlaceholder_root {
  color: #9197a3;
  position: absolute;
  z-index: 0;
}
.public_DraftEditorPlaceholder_inner {
  white-space: pre-wrap;
}
.DraftEditor__container {
  background-color: rgba(255, 255, 255, 0);
  border-left: 0.1px solid transparent;
  position: relative;
  z-index: 1;
  height: inherit;
  text-align: initial;
}

.draftEditorContent {
  outline: none;
  user-select: text;
  white-space: pre-wrap;
  overflow-wrap: break-word;
  -webkit-user-modify: read-write-plaintext-only;
  height: inherit;
  text-align: initial;
}
.draftStyleDefaultBlock {
  direction: ltr;
  text-align: left;
  position: relative;
  white-space: pre-wrap;
  margin: 0.5em 0;
}
.text_limit_block {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  color: #4a4a4a;
  font-size: 14px;
}
.text_limit_limit {
  margin-left: 10px;
}
.EventAudioInner {
  background-color: rgba(1, 169, 224, 0.07);
