}
.EventAudio__audios {
  padding: 0;
  width: 100%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.EventAudio__dropzone {
  width: 100%;
  height: 100px;
  font-size: 20px;
  color: rgb(0, 154, 214);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;
}
.EventAudio__dropzone_items {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 10px;
}
.EventAudio__dropzone_icon {
  content: "";
  display: inline-flex;
  width: 40px;
  height: 43px;
  background-image: url("../../assets/icons/audio.svg");
  background-repeat: no-repeat;
  transition: all 0.2s;
}
