.EventAudio__dropzone_text {
  display: inline-flex;
  width: 75%;
}
.EventModule_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  background-color: #ffffff;
}
.EventModule_name {
  color: #000000;
  transition: 0.3s;
  font-size: 20px;
  display: inline-block;
  cursor: pointer;
  font-weight: normal;
}
.SwitchWrapper {
  flex-shrink: 0;
  cursor: pointer;
  width: 50px;
  height: 24px;
}
.Switch__icon {
  background-color: rgb(0, 154, 214);
  width: 100%;
  height: 100%;
  border-radius: 500px;
  position: relative;
  cursor: pointer;
  transition: 0.5s;
}
.Switch__icon::after {
  content: "";
  width: 18px;
  height: 18px;
  margin: 3px;
  background-color: #ffffff;
  position: absolute;
  transform: translateX(0%);
  border-radius: 50%;
  transition: 0.5s;
  transform: translateX(26px);
}
.Switch__input {
  display: none;
}
.EventModule_content {
  visibility: visible;
  margin-top: 0;
  height: auto;
  opacity: 1;
  transition: 0.5s;
  padding: 20px 0 0 0;
}
.EventBonusLine {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.InputFloatingLabel__input {
  border-radius: 0;
  display: flex;
  font-size: 100%;
  line-height: 25px;
  border: 0;
  color: #000000;
  flex: 1 1 auto;
  outline: none;
  order: 2;
}
.InputFloatingLabel__label {
  color: #98a6ad;
  order: 1;
  padding-left: 2px;
  pointer-events: none;
  transform-origin: left top;
  transition: 200ms ease all;
}
.InputFloatingLabel__label:focus {
  opacity: 1;
  transform: scale(0.8) translate3d(0, 5px, 0);
}
.InputFloatingLabel__bar {
  position: relative;
  width: inherit;
  height: 2px;
  display: block;
  order: 3;
  bottom: 0;
  transition: 200ms ease all;
  background-color: rgba(0, 0, 0, 0.1);
}
.EventBonus__note {
  margin-left: 10px;
  margin-top: 30px;
  margin-bottom: 0;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
}
.HelpTooltip__button {
  width: 20px;
  height: 20px;
  margin: 0 0 0 7px;
  padding: 1px 0 0 0;
  display: inline-block;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  border-radius: 50%;
  font-style: normal;
  background-color: rgba(0, 0, 0, 0.07);
}
.__react_component_tooltip {
  margin-top: -10px;
  color: #fff;
  background-color: #222;
  border-radius: 3px;
  display: inline-block;
  font-size: 13px;
  left: -999em;
  opacity: 0;
  padding: 8px 21px;
  position: fixed;
  pointer-events: none;
  transition: opacity 0.3s ease-out;
  top: -999em;
  visibility: hidden;
  z-index: 9999 !important;
  font-weight: bold !important;
}
.Switch__wrapper {
  width: 50px;
  height: 24px;
  flex-shrink: 0;
  cursor: pointer;
}
.EventTimeLimitSetting__line {
  display: flex;
  margin-bottom: 10px;
}
.InputFloatingLabel__wrapper {
  display: flex;
  flex-direction: column;
}
.InputFloatingLabel__input {
  border-radius: 0;
  display: flex;
  font-size: 100%;
  line-height: 25px;
  border: 0;
  color: #000000;
  flex: 1 1 auto;
  order: 2;
  margin-top: 30px;
}
.InputFloatingLabel__label {
  color: #98a6ad;
  font-weight: normal;
  opacity: 0.75;
  order: 1;
  padding-left: 2px;
  pointer-events: none;
  transform-origin: left top;
  transform: scale(1) translate3d(0, 22px, 0);
  transition: 200ms ease all;
}
.InputFloatingLabel__label:focus {
  opacity: 1;
  transform: scale(0.8) translate3d(0, 5px, 0);
}
.InputFloatingLabel__bar {
  height: 2px;
  display: block;
  order: 3;
  bottom: 0;
  transition: 200ms ease all;
  background-color: rgba(0, 0, 0, 0.1);
}
.AsideEventControls__content {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 18px;
  padding: 15px 15px 0 15px;
  min-height: 100px;
  padding-bottom: 1px;
}
.EventControls__content__next {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 20px;
}
.EventAdd {
  position: relative;
  margin-bottom: 25px;
}
.nav__link {
  padding: 0;
  display: block;
  font-weight: normal;
  font-size: 14px;
}
.InputLine__wrapper {
  padding: 0.444em 0.6em;
  position: relative;
  z-index: 1;
  font-size: inherit;
}
.InputLine__input {
  padding: 6px 12px;
  width: 100%;
  border: 0 !important;
  border-radius: 0 !important;
  background: transparent;
  outline: 0;
  font-size: inherit;
}
.InputLine__view {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
  transition: 0.3s;
  z-index: -1;
}
.InputLine__button {
  position: absolute;
  top: 0;
  right: 10px;
  bottom: 0;
  background: transparent;
  border: 0;
  opacity: 0.5;
  transition: 0.3s;
}
.Switch__wrapper_time {
  flex-shrink: 0;
  width: 50px;
  height: 24px;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  cursor: pointer;
}
.Switch__input_time {
  display: none;
  font-size: 16px;
  outline: none;
}
.Switch__icon_time {
  width: 100%;
