.container {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 200px;
  height: calc(100% - 50px);
  flex-shrink: 0;
  background-color: #ffffff;
  box-shadow: 0px 0px 15px rgba(0, 0, 100, 0.1);
  font-size: 14px;
  transition: all 0.4s;
  z-index: 1;
}
.nav {
  background: radial-gradient(
      farthest-side at 50% 0,
      rgba(0, 0, 0, 0.15),
      rgba(0, 0, 0, 0)
    ),
    linear-gradient(rgba(255, 255, 255, 0), #ffffff 70%) 0 100%;
  background-repeat: no-repeat;
  background-color: #ffffff;
  background-size: 100% 10px, 100% 25px;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.EventAdd {
  margin: 0;
  position: relative;
}
.nav__link {
  display: block;
  padding: 10px 10px 10px 20px;
  font-weight: normal;
  color: #000000;
  text-decoration: none;
}
.nav__link:hover {
  background: #f3f3f3;
}
.InputLine {
  padding: 0.444em 0.6em;
  position: relative;
  z-index: 1;
  font-size: inherit;
}
.InputLine__input {
  padding: 10px 20px 10px 20px;
  max-width: 200px;
  box-sizing: border-box;
  width: 100%;
  border: 0 !important;
  border-radius: 0 !important;
  background: transparent;
  outline: 0;
  font-size: inherit;
}
.InputLine__view {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
  transition: 0.3s;
  z-index: -1;
}
.InputLine__button {
  position: absolute;
  top: 0;
  right: 10px;
  bottom: 0;
  background: transparent;
  border: 0;
  opacity: 0.5;
  transition: 0.3s;
  font-size: 16px;
  outline: none;
}
.InputLine__button::after {
  content: "\21B5";
  font-size: 14px;
}
.Icon__codes {
  width: 1.4em;
  height: 1.4em;
  margin-right: 8px;
  background-image: url("../../assets/icons/a9z.svg");
  background-size: 100%;

  display: inline-block;
  vertical-align: middle;
  background-repeat: no-repeat;
  background-position: center;
}

.Icon__map {
  width: 1.4em;
  height: 1.4em;
  margin-right: 8px;
  background: url("../../assets/icons/map.svg");

  display: inline-block;
  vertical-align: middle;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.nav__heading {
  padding: 10px 20px;
  font-size: 12px;
  color: #98a6ad;
}

.quest__part {
  pointer-events: auto;
  touch-action: none;
}

.EventNavLink {
  color: rgb(0, 154, 214);
  background-color: rgba(1, 169, 224, 0.07);
  cursor: default;
  display: block;
  padding: 10px 10px 10px 20px;
  font-weight: normal;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  outline: 0;
  text-decoration: none;
}

.EventNavLink__wrapper {
  display: -ms-flexbox !important;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}

.EventNavLink__text {
  width: 75%;
}
.EventNavLink__order {
  display: inline-block;
  opacity: 0.7;
  margin-right: 5px;
}

.icon_error_validation {
  width: 10px;
  height: 10px;
  background-color: #f79494;
  border-radius: 50%;
  margin: 0 5px;
  margin-top: 1px;
  display: inline-block;
  vertical-align: middle;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.icon_drag_point {
  opacity: 0;
  width: 12px;
  height: 12px;
  margin-right: 8px;
  background-image: url("../../assets//icons/error.svg");
  background-size: 100%;
  cursor: grab;
  transition: 0.3s;
  flex-shrink: 0;
  margin-left: 5px;

  display: inline-block;
  vertical-align: middle;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.EventNavLink__images {
  margin-top: 5px;
  width: 100%;
}
