.Main__container {
  background: rgb(245, 247, 248);
}

.Main__dashboard {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  margin-top: 50px;
}

.Main__content {
  padding: 20px;
}

.Main__article {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 33px;
}
.Main__article button {
  background-color: rgb(0, 154, 214);
  border-radius: 5px;
  flex-shrink: 0;
  padding: 4px 25px 5px 25px;
  box-shadow: 0 2px 0 0 rgb(0, 123, 171);
  color: #ffffff;
  font-size: 18px;
  border: 0;
  cursor: pointer;
  outline: none;
  box-sizing: border-box;
  height: 33px;
}
.Main__article h2 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
.Main__article h2 span {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: normal;
}

.Main__quests_list {
  margin-top: 20px;
  box-sizing: border-box;
  display: flex;
  flex-flow: row wrap;
  margin-right: -0.5rem;
  margin-left: -0.5rem;
}

.link {
  text-decoration: none;
}

.Main__quests_list_item {
  flex-basis: 25%;
  max-width: 25%;
  box-sizing: border-box;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
}

.Quest {
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.02);
  min-height: 100px;
  margin-bottom: 20px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  vertical-align: top;
  position: relative;
  font-size: 14px;
  user-select: none;
  overflow: hidden;
}

.Quest a {
  color: #000000;
  text-decoration: none;
}

.Quest__inner-top {
  position: relative;
}
.Quest__inner {
  width: 100%;
  padding: 13px 17px 18px 17px;
}

.Quest__img {
  background-position: center;
  height: 160px;
  background-repeat: no-repeat;
  background-size: cover;
}
.Quest__status {
  position: absolute;
  top: 0px;
  height: 30px;
  margin: 17px;
  border-radius: 6px;
  padding-right: 7px;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  font-weight: bold;
  font-size: 12px;
  line-height: 30px;
  -webkit-font-smoothing: antialiased;
  text-transform: uppercase;
}
.Quest__status:before {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  content: "";
  width: 10px;
  height: 10px;
  margin: 10px 7px;
  background-color: #f5a623;
  border-radius: 100%;
}
.Quest__actions {
  position: absolute;
  top: 0px;
  right: 0px;
}
.Quest__actions-btn {
  width: 30px;
  height: 30px;
  margin: 17px 17px 0 0;
  border-radius: 6px;
  background: rgba(0, 0, 0, 0.5) url("../../assets/icons/more.svg") no-repeat;
  background-position: center;
  background-size: 18px;
  border: 0;
}
.Quest__no-img {
  background: #f8f8f8 url("../../assets/icons/no-img.svg") no-repeat;
  background-position: center;
}

.Quest__count {
  margin-bottom: 5px;
  font-size: 15px;
  font-weight: bold;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  color: #979797;
}

.Quest__header {
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  margin-bottom: 9px;
}
.Quest__header h2 {
  margin: 0;
  padding: 0;
}

.Quest__last-modidied {
  font-size: 13px;
  line-height: 15px;
  color: #7e7e7e;
}

.VirtualLabel {
  display: inline-block;
  margin-bottom: 8px;
  margin-left: 3px;
  margin-right: 3px;
  border-radius: 8px;
  padding: 0 5px;
  background: #b800ff;
  color: #ffffff;
  font-weight: bold;
  font-size: 12px;
  line-height: 18px;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.Quest__heading {
  font-size: 1.2em;
  padding-right: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translateZ(0px);
  transform: translateZ(0px);
}
.QuestStatus {
  color: #979797;
}
.QuestStatus_message {
  margin-left: 3px;
  font-size: 13px;
}
