.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #00000049;
}

.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  min-width: 492px;
  max-height: 95%;
  margin: auto;
  border-radius: 10px;
  padding: 40px;
  overflow: auto;
  background: white;
  box-sizing: border-box;
}

.header {
  width: auto;
  padding: 0 0 11px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background: #ffffff;
}
.header__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  letter-spacing: -0.41px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.header__close {
  width: 17px;
  height: 17px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  margin-right: 10px;
  line-height: 25px;
  font-size: 34px;
  color: #000000;
  background: transparent;
  border: 0;
  background-image: url("../assets/icons/close-svgrepo-com.svg");
  font-family: "Open Sans", "Helvetica Neue", Arial, sans-serif;
  outline: none;
}
.content {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 0;
}

.inputWithButtons {
  padding: 14px 16px 12px 16px;
  align-items: center;
  margin: 20px 0;

  position: relative;
  z-index: 1;
  font-size: inherit;
  display: inline-flex;
  flex-wrap: wrap;
}

.inputWithButtons__inputWrapper {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
}

.inputWithButtons__input {
  padding: 0;
  text-align: left;
  width: 100%;
  border: 0 !important;
  border-radius: 0 !important;
  background: transparent;
  outline: 0;
  font-size: inherit;
  -webkit-box-flex: 1;
}

.inputWithButtons__view {
  border: 1px solid #000000 !important;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  border: 0;
  transition: 0.3s;
  z-index: -1;
  display: block;
}
.virtual {
  display: flex;
  justify-content: space-between;
}

.virtual__text {
  display: flex;
  align-items: center;
  font-weight: 600;
}

.virtual__switch_wrapper {
  width: 50px;
  height: 24px;
  flex-shrink: 0;
}
.switch__input {
  display: none;
  font-size: 16px;
  font-family: "Open Sans", "Helvetica Neue", Arial, sans-serif;
  outline: none;
}
.switch__icon {
  width: 100%;
  height: 100%;
  border-radius: 500px;
  background-color: rgba(152, 166, 173, 0.45);
  position: relative;
  cursor: pointer;
  transition: 0.5s;
}
.switch__icon::after {
  content: "";
  width: 18px;
  height: 18px;
  margin: 3px;
  background-color: #ffffff;
  position: absolute;
  transform: translateX(0%);
  border-radius: 50%;
  transition: 0.5s;
}

.btns {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  margin-top: 35px;
}

.btns__decline,
.btns__add {
  border: 0;
  padding: 13px 40px 15px;
  background: #1da1f3;
  color: #ffffff;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  font-size: 18px;
  line-height: 22px;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  transition: 0.3s ease;
  cursor: pointer;
}

.btns__decline {
  margin-right: 20px;
  background: #ffffff;
  color: #000000;
}